import http from '@/http';
import CONFIG from '@/assets/js/config';

// SKU
export function getSKURepairInfo (params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    };
    return http.getAjax('/sample_control/sample_check_report/spu_edit_list', data);
}

//  - 
export function getBufferPackInfo (params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    };
    return http.getAjax('/sample_control/sample_check_report/get_buffer_pack_info', data);
}

//  - 
export function getShipmentPackInfo (params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    };
    return http.getAjax('/sample_control/sample_check_report/get_shipment_pack_info', data);
}

// SKU
export function syncSkuInfo (params) {
    for (let [key, value] of Object.entries(CONFIG.ajaxData)) {
        params.append(key, value);
    }
    return http.fileAjax('/sample_control/sample_check_report/sync', params);
}

// SKU
export function submitSaveSKURepairInfo (params) {
    for (let [key, value] of Object.entries(CONFIG.ajaxData)) {
        params.append(key, value);
    }
    return http.fileAjax('/sample_control/sample_check_report/save', params);
}

// SKU
export function auditSkuInfo (params) {
    for (let [key, value] of Object.entries(CONFIG.ajaxData)) {
        params.append(key, value);
    }
    return http.fileAjax('/sample_control/sample_check_report/check', params);
}

// SKU
export function rejectSkuInfo (params) {
    for (let [key, value] of Object.entries(CONFIG.ajaxData)) {
        params.append(key, value);
    }
    return http.fileAjax('/sample_control/sample_check_report/reject', params);
}



