import http from '@/http'
import CONFIG from '@/assets/js/config'

// 
export function getSampleInspectListInfo (queryData = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...queryData
    }

    return http.getAjax('/sample_control/sample_quality_control/check_pending_list', data)
}

// 
export function handleTask (queryData = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...queryData
    }

    return http.getAjax('/sample_control/sample_quality_control/handle_task', data)

}

// 
export function getAssignGroup(params = {}){
    const data = {
        ...CONFIG.ajaxData,
        ...params
    }
    return http.getAjax('/sample_control/sample_check_report/get_group_user', data);
}

// 
export function getRejectType(params = {}){
    const data = {
        ...CONFIG.ajaxData,
        ...params
    }
    return http.getAjax('/sample_control/sample_check_report/get_reject_type', data);
}

// spu
export function sendBatchReject(params = {}){
    const data = {
        ...CONFIG.ajaxData,
        ...params
    }
    return http.postAjax('/sample_control/sample_check_report/batch_reject', data);
}



