// SPU //1 2 3  4
export function SPUOperationInfo(params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    };
    switch (data.status) {
        case 1:
            return http.postAjax('/product/Product_list/cancel_development', data);
        case 2:
            return http.postAjax('/product/Product_list/re_development', data);
        case 3:
            return http.postAjax('/product/Product_list/mark_anomaiy', data);
        case 4:
            return http.postAjax('/product/Product_list/to_examine', data);
    }
}

// SKU
export function getRepairSKUState(params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    };
    return http.getAjax('/product/product/query_stats', data);
}


