import http from '@/http';
import CONFIG from '@/assets/js/config';

// 
export function getRejectedListInfo (queryData = {}) {
    const data = {
        limit: 20,
        offset: 1,
        search_type: 1,
        ...CONFIG.ajaxData,
        ...queryData
    };

    return http.getAjax('/rejection/rejection/reject_config_list', data);

}

//
export function delRejected (params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    };

    return http.postAjax('/rejection/rejection/del_reject_config', data);
}

// 
export function changeRejectedInfo (params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    };

    return http.postAjax('/rejection/rejection/edit_reject_config', data);
}

//
export function addRejectedInfo (params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    };

    return http.postAjax('/rejection/rejection/add_config', data);
}

// excel
export function exportRejectedInfo (params = {}) {
    const data = {
        ...params
    };
    let str = '?';
    for (let key in data) {
        str = `${str}&${key}=${data[key] || ''}`;
    }
    window.open('/rejection/rejection/export_reject_config' + str, '_self');
}

// excel
export function importRejectedInfo (params = {}) {
    const data = {
        ...params
    };

    return http.postAjax('/rejection/rejection/import_reject_config', data);
}



