import http from '@/http';
import CONFIG from '@/assets/js/config';

// 
export function getGoodsModificationListInfo (queryData = {}) {
    const data = {
        limit: 20,
        offset: 1,
        search_type: 1,
        ...CONFIG.ajaxData,
        ...queryData
    };
    return http.getAjax('/design/product_list/product_list', data);

}

// 
export function getModificationWorkerList (queryData = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...queryData
    };
    return http.getAjax('/design/product_list/get_group_user', data);
}

// 
export function submitModificationGoods (queryData = {}) {
    const data = {
        search_type: 1,
        ...CONFIG.ajaxData,
        ...queryData
    };
    return http.postAjax('/design/product_list/spu_photo_complete', data);
}

// 
export function assignTask (queryData = {}) {
    const data = {
        search_type: 1,
        ...CONFIG.ajaxData,
        ...queryData
    };
    return http.getAjax('/design/product_list/spu_assign', data);
}

// 
export function getRejectInfo (queryData = {}) {
    const data = {
        search_type: 1,
        ...CONFIG.ajaxData,
        ...queryData
    };
    return http.getAjax('/design/product_list/reject_reason_list', data);
}

// 
export function modificationGoodsReject (queryData = {}) {
    const data = {
        search_type: 1,
        ...CONFIG.ajaxData,
        ...queryData
    };
    return http.postAjax('/design/product_list/spu_operate_reject', data);
}

//
export function getModificationDetailInfo (queryData = {}) {
    const data = {
        search_type: 1,
        ...CONFIG.ajaxData,
        ...queryData
    };
    return http.getAjax('/design/product_list/spu_detail', data);
}



