import http from '@/http';
import CONFIG from '@/assets/js/config';

// 
export function getPhotographyGoodsListInfo (queryData = {}) {
    const data = {
        limit: 20,
        offset: 1,
        search_type: 1,
        ...CONFIG.ajaxData,
        ...queryData
    };
    return http.getAjax('/photo/product_list/product_list', data);
}

// 
export function getPhotographyWorkerList (queryData = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...queryData
    };
    return http.getAjax('/photo/product_list/get_group_user', data);
}

// 
export function submitPhotographyGoods (queryData = {}) {
    const data = {
        search_type: 1,
        ...CONFIG.ajaxData,
        ...queryData
    };
    return http.postAjax('/photo/product_list/spu_photo_complete', data);
}

// 
export function assignTask (queryData = {}) {
    const data = {
        search_type: 1,
        ...CONFIG.ajaxData,
        ...queryData
    };
    return http.getAjax('/photo/product_list/spu_assign', data);
}

// 
export function getRejectInfo (queryData = {}) {
    const data = {
        search_type: 1,
        ...CONFIG.ajaxData,
        ...queryData
    };
    return http.getAjax('/photo/product_list/reject_reason_list', data);
}

// 
export function photographyGoodsReject (queryData = {}) {
    const data = {
        search_type: 1,
        ...CONFIG.ajaxData,
        ...queryData
    };
    return http.postAjax('/photo/product_list/spu_operate_reject', data);
}




